	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Major Medical Breakthrough","date":"01 Dec 2006","slug":"/2006-12-01-major-medical-breakthrough/","tags":"Y2006"},"html":"<p>In a breakthrough for German medicine, researchers have revealed that <a href=\"http://claireseuroamerica.blogspot.com/2006/11/small-victories.html\">cold kidneys do not cause disease</a>. This has the potential to save millions for the German health care system, as until now it has been thought that cold kidneys are the second leading cause of death in Germany, just ahead of being exposed to drafts and behind the number one killer, a <a href=\"http://www.spiegel.de/international/0,1518,416475,00.html\" title=\"http://www.spiegel.de/international/0,1518,416475,00.html\">range of circulatory disorders</a> known as <em>Kreislaufkollaps</em>.</p>\n<p>Research into these conditions has been all the more difficult as they seem only to occur in Germany, and only to afflict native-born Germans. Finding cures could give hope that Germans could achieve of level of health on par with that of the rest of the world, although it would also eliminate the number one topic of conversation in the country. Discussion would then have to resort to other subjects such as other people's poor driving and the condition of the neighbors' gardens.</p>\n<p>(A note to my British readers: cold kidney is a fictitious medical condition, and not something you eat.)</p>"},"next":{"fields":{"title":"Should Americans Be Allowed to Marry?","date":"30 Nov 2006","slug":"/2006-11-30-should-americans-be-allowed-to-marry/"}},"previous":{"fields":{"title":"Jim Knopf and Christopher","date":"02 Dec 2006","slug":"/2006-12-02-jim-knopf-and-christopher/"}}},{"node":{"fields":{"title":"Should Americans Be Allowed to Marry?","date":"30 Nov 2006","slug":"/2006-11-30-should-americans-be-allowed-to-marry/","tags":"Y2006"},"html":"<p>In New Zealand they are <a href=\"http://someofmybestfriendsareamerican.cf.huffingtonpost.com/huffington.mov\">not so sure</a> (Quicktime 14MB) (<a href=\"http://www.inactualfact.com/?p=287\">via In Actual Fact</a>, and no, I hadn't seen this before).</p>"},"next":{"fields":{"title":"Perfect Family Christmas","date":"30 Nov 2006","slug":"/2006-11-30-perfect-family-christmas/"}},"previous":{"fields":{"title":"Major Medical Breakthrough","date":"01 Dec 2006","slug":"/2006-12-01-major-medical-breakthrough/"}}},{"node":{"fields":{"title":"Perfect Family Christmas","date":"30 Nov 2006","slug":"/2006-11-30-perfect-family-christmas/","tags":"Y2006"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2006/11/perfect_family_christmas.jpg\" alt=\"perfect family Christmas\" /></p>\n<p>As the first of December approaches and the first Sunday in Advent, I thought we would share this envelope we received in the mail that pictures the perfect family Christmas... in our worst nightmare! If these children show up at your house, have the parents arrested for child abuse. That girl especially... she's either the <a href=\"http://www.wonkette.com/politics/capitol-hill/congressional-catfight-harris-vs-pelosi-175045.php\">young Nancy Pelosi</a> or the <a href=\"http://www.wonkette.com/politics/halloween/chilling-cocktobers-house-of-representatives-211355.php\">young Laura Bush</a>.</p>"},"next":{"fields":{"title":"Are Eggs Liquids?","date":"30 Nov 2006","slug":"/2006-11-30-are-eggs-liquids/"}},"previous":{"fields":{"title":"Should Americans Be Allowed to Marry?","date":"30 Nov 2006","slug":"/2006-11-30-should-americans-be-allowed-to-marry/"}}}],"pathPrefix":"","first":false,"last":false,"index":175,"pageCount":1101}}

